*{
    margin: 0;
    padding: 0;
}
.navbar img{
    width: 159px;
    height: 69px;
}
.navbar{
    display: flex;
    padding-left: 70px;
    justify-content: space-between;
    background-color:rgb(18, 18, 18);
    box-shadow: rgba(0, 0, 0, 0.35) 0px 5px 15px;
}
ul{
    display: flex;
    color: aliceblue;
}
ul li{
    /* margin: 19px 32px; */
    margin-right: 55px;
    margin-top: 10px;
    text-decoration: none;
    /* list-style: none; */
    list-style-type: none;
}
.linknv{
    text-decoration: none;
    color: inherit;
}
.linknv li{
    padding: 10px;
}
.linknv li:hover{
    background-color:white;
    border-radius: 10px;
}
